import { driver } from "driver.js"; // import driver.js
import "driver.js/dist/driver.css"; // import driver.js css
import { Button, Alert } from "antd";
import steps from "./steps";

const Guide = () => {
	const driverObj = driver({
		showProgress: true,
		steps,
		animate: true, // 
		opacity: 0.75, // 0
		doneBtnText: "", // 
		closeBtnText: "", // 
		nextBtnText: "", // 
		prevBtnText: "" // 
	});

	const guide = () => {
		driverObj.drive();
		console.log(driverObj, "driverObj");
	};
	return (
		<div className="card content-box">
			<Alert
				message=" Demo  driver.js."
				style={{ width: "100%" }}
				type="warning"
			/>
			<div style={{ margin: "15px auto" }}>
				<Button type="primary" id="antd-button" onClick={guide}>
					 
				</Button>
			</div>
		</div>
	);
};

export default Guide;
